import * as React from "react";
import { Box, Typography } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import { Link as RouterLink } from "gatsby";
import { Client } from "../globals";

export default function Services({ active, onServiceClick, iconProps, title }) {
  const theme = useTheme();
  title =
    title === undefined ? "Ci prendiamo cura dei vostri denti dal 1998" : title;
  const { services } = Client;

  return (
    <>
      {title && (
        <Typography variant="h6" sx={{ mb: 2, fontStyle: "italic" }}>
          {title}
        </Typography>
      )}
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "repeat(7, minmax(0, 1fr ))",
          gridTemplateRows: "repeat(2, minmax(0, 1fr))",
          gap: 1,
          borderCollapse: "collapse",
          // gridAutoColumns: "1fr",
          // height: 200,
          "& > *": {
            // border: "1px solid lightgray",
            px: 2,
            py: 0,
            textAlign: "center",
            height: "100%",
            boxSizing: "border-box",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            gap: 2,
            justifyContent: "center"
          },
          "& > *:hover": {
            color: theme.palette.primary.light,
            cursor: "pointer"
          },
          [theme.breakpoints.only("sm")]: {
            gridTemplateColumns: "repeat(5, minmax(0, 1fr))",
            gridTemplateRows: "repeat(3, minmax(0, 1fr))"
          },
          [theme.breakpoints.only("xs")]: {
            gridTemplateColumns: "repeat(3, minmax(0, 1fr))",
            gridTemplateRows: "repeat(5, minmax(0, 1fr))"
          }
        }}
      >
        {services.map(({ icon, label, name, description, img }) => (
          <Box
            component="article"
            name={name}
            key={name}
            sx={{
              backgroundColor: active === name ? "primary.soft" : "inherit",
              borderRadius: "1rem"
            }}
          >
            <RouterLink
              to={`/trattamenti/${name}`}
              style={{ color: "unset", textDecoration: "inherit" }}
              onClick={(event) =>
                onServiceClick &&
                onServiceClick(event, { description, img, icon, label, name })
              }
            >
              {icon({ height: "2.25rem", ...(iconProps || {}) })}
              <Typography component="h6" sx={{ fontSize: 13, m: 0 }}>
                {label}
              </Typography>
            </RouterLink>
          </Box>
        ))}
      </Box>
    </>
  );
}
