import * as React from "react";
import {
  Card,
  CardContent,
  CardHeader,
  Typography,
  // Avatar
} from "@mui/material";
// import FormatQuoteIcon from "@mui/icons-material/FormatQuote";

const ReviewCard = ({ review }) => {
  const { author, title, content,
    // avatar 
  } = review;

  return (
    <Card sx={{ flexGrow: 1 }}>
      <CardHeader
        title={title}
        subheader={author}
      // avatar={avatar || <Avatar />}
      />
      {/* <FormatQuoteIcon
        sx={{ mr: "auto", pl: "25%", pt: 2, display: "block" }}
      /> */}
      <CardContent>
        <Typography
          variant="body2"
          color="text.secondary"
          sx={{ textAlign: "justify" }}
        >
          <em>{content}</em>
        </Typography>
      </CardContent>

    </Card>
  );
};

export default ReviewCard;
