import * as React from "react";
import Services from "../cra-pages/Services/Services";
import Layout from "../layouts/PageWithHeaderAndFooter";
import Seo from "../components/Seo";
import { Client } from "../globals";

const ServicesPage = ({ path, pageContext }) => {
  const { name } = pageContext;
  const service = Client.services.find((sv) => sv.name === (name || "implantologia"))
  return (
    <>
      <Seo
        title={`Trattamenti ${service.label}`}
        keywords={service.keywords}
        description={`Trattamenti di ${service.label} allo Studio Novident di Vergiate`}
        image={{ src: `/icons/${name}.png`, width: 144, height: 144 }}
        path={path}
      />
      <Layout>
        <Services name={name} />
      </Layout>
    </>
  )
}

export default ServicesPage