import * as React from "react";
import { useState } from "react";
import { Box, Typography, Button, Paper } from "@mui/material";
import { styled } from "@mui/material/styles";
import { Client } from "../../../globals";
import ReviewCard from "../../../components/common/ReviewCard";
// import { useTheme } from "@mui/material/styles";
// import useMediaQuery from "@mui/material/useMediaQuery";

const { reviews } = Client;

const ReviewsContainer = styled(Box)(({ theme }) => ({
  position: "relative",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  padding: theme.spacing(2),
  [theme.breakpoints.up("md")]: {}
}));

const StyledButton = styled(Button)(({ theme }) => ({
  borderRadius: "2rem",
  minWidth: "3rem",
  flexGrow: 0,
  padding: theme.spacing(1),
  backgroundColor: theme.palette.primary.main,
  color: theme.palette.common.white,
  boxShadow: theme.shadows[2],
  "&:hover": {
    backgroundColor: theme.palette.primary.dark
  }
}));

const Reviews = () => {
  // const theme = useTheme();
  // const isMd = useMediaQuery(theme.breakpoints.up("md"));
  const [currentReviewIndex, setCurrentReviewIndex] = useState(0);
  const [direction, setDirection] = useState(null);

  const prevIndex = (index) => {
    if (index === 0) {
      return reviews.length - 1;
    } else {
      return index - 1;
    }
  };

  const nextIndex = (index) => {
    if (index === reviews.length - 1) {
      return 0;
    } else {
      return index + 1;
    }
  };

  const handlePrevReview = () => {
    setCurrentReviewIndex(prevIndex);
    setDirection("left");
  };

  const handleNextReview = () => {
    setCurrentReviewIndex(nextIndex);
    setDirection("right");
  };

  const handleSlideEnd = () => {
    setDirection(null);
  };

  return (
    <ReviewsContainer>
      <Box sx={{ display: "flex", justifyContent: "center", my: 2 }}>
        <Typography variant="h4" sx={{ fontWeight: "bold" }}>
          Dicono di noi
        </Typography>
      </Box>
      <Paper
        sx={{
          width: "100%",
          display: "flex",
          justifyContent: "space-evenly",
          flexDirection: "row",
          backgroundColor: "secondary.soft"
        }}
      >
        <StyledButton onClick={handlePrevReview}>{`<`}</StyledButton>
        {/* {isMd && (
          <ReviewCard
            review={reviews[prevIndex(currentReviewIndex)]}
            direction={direction}
            onSlideEnd={handleSlideEnd}
          />
        )} */}
        <ReviewCard
          review={reviews[currentReviewIndex]}
          direction={direction}
          onSlideEnd={handleSlideEnd}
        />
        {/* {isMd && (
          <ReviewCard
            review={reviews[nextIndex(currentReviewIndex)]}
            direction={direction}
            onSlideEnd={handleSlideEnd}
          />
        )} */}
        <StyledButton onClick={handleNextReview}>{`>`}</StyledButton>
      </Paper>
    </ReviewsContainer>
  );
};

export default Reviews;
